import { Sortable } from '@shopify/draggable';

const categories = document.querySelectorAll('.kanban-category');
const links = document.querySelectorAll('.kanban-add-link');
const forms = document.querySelectorAll('.kanban-add-form');

function toggleItems(el) {
  const parent = el.closest('.kanban-add');
  const card = parent.querySelector('.card');
  const link = parent.querySelector('.kanban-add-link');
  const form = parent.querySelector('.kanban-add-form');

  link.classList.toggle('d-none');
  form.classList.toggle('d-none');

  if (card && card.classList.contains('card-sm')) {
    if (card.classList.contains('card-flush')) {
      card.classList.remove('card-flush');
    } else {
      card.classList.add('card-flush');
    }
  }
}

const KANBAN_SEL = {
  category: '.kanban-category',
  item: '.kanban-item',
  title: '.card-header-title',
  timeBlocks: '.col-auto.text-secondary'
};

function closestCategory(el) {
  const card = el.closest('.card');
  return card ? card.querySelector(KANBAN_SEL.category) : null;
}

function findCategoryBy(employeeId, dateStr) {
  if (String(employeeId) === '0') {
    return document.querySelector('.kanban-category[id="0"]') 
        || document.getElementById('0'); // запасной вариант
  }
  const cats = document.querySelectorAll(KANBAN_SEL.category);
  for (const c of cats) {
    if (String(c.id) === String(employeeId) &&
        c.getAttribute('data-associated-date') === dateStr) {
      return c;
    }
  }
  return null;
}
function setScissorsHandler(rootNode, seconds, id) {
  const a = rootNode.querySelector('i.fe.fe-scissors')?.closest('a');
  if (a) a.setAttribute('onclick', `controlModal(${Number(seconds) || 0}, ${id})`);
}

function getTimeSpentSeconds(node) {
  // Берём ПЕРВЫЙ блок времени (списанное) и читаем текст из <small>
  const spentBlock = node.querySelectorAll(KANBAN_SEL.timeBlocks)[0];
  if (!spentBlock) return 0;

  // приоритет: data-атрибут в секундах, иначе парсим "hh:mm:ss" из small
  const attr = spentBlock.getAttribute('data-time-spent');
  if (attr && /^\d+$/.test(attr)) return parseInt(attr, 10);

  const small = spentBlock.querySelector('small');
  const raw = small ? small.textContent.trim() : '00:00:00';
  const m = raw.match(/^(\d{2}):(\d{2}):(\d{2})$/);
  if (!m) return 0;
  return (+m[1]) * 3600 + (+m[2]) * 60 + (+m[3]);
}
function updateCardTimeSeconds(node, seconds) {
  // второй блок — «план», первый — «списано»
  const blocks = node.querySelectorAll(KANBAN_SEL.timeBlocks);
  const holder = blocks[1] || blocks[0];
  if (!holder) return;

  holder.setAttribute('data-time', String(seconds));

  // пишем текст внутрь <small>; если его нет — в сам holder
  const small = holder.querySelector('small') || holder;
  small.textContent = floatToTime(seconds);
}
function siblingsByBitrix(bitrixId) {
  // ищем элементы с таким же data-bs-bitrix (и .kanban-item, и клон .card.shadow.mb-3)
  return Array.from(document.querySelectorAll(`[data-bs-bitrix="${bitrixId}"]`))
    .map(el => el.closest('.kanban-item') || el); // приводим к контейнеру карточки
}

function hasTrackedSibling(bitrixId, excludeLocalId) {
  const list = siblingsByBitrix(bitrixId);
  for (const n of list) {
    const id = parseInt(n.id, 10) || 0;
    if (excludeLocalId && id === excludeLocalId) continue;
    if (getTimeSpentSeconds(n) > 0) return true;
  }
  return false;
}
function ensureCardExistsLike(sourceNode, id, attrs = {}) {
  // ищем карточку по id, если нет — клонируем эталон и минимально правим
  let node = document.getElementById(String(id));
  if (node) return node;
  const clone = sourceNode.cloneNode(true);
  clone.id = String(id);
  if ('title' in attrs) {
    const t = clone.querySelector(KANBAN_SEL.title);
    if (t) t.textContent = attrs.title;
  }
  if ('bitrix' in attrs) clone.setAttribute('data-bs-bitrix', attrs.bitrix);
  let secs = 0;
  if ('time' in attrs) {
    secs = Number(attrs.time) || 0;
    updateCardTimeSeconds(clone, secs);
  } else {
    const holder = (clone.querySelectorAll(KANBAN_SEL.timeBlocks)[1] || clone.querySelectorAll(KANBAN_SEL.timeBlocks)[0]);
    secs = Number(holder?.getAttribute('data-time')) || 0;
  }
  setScissorsHandler(clone, secs, id);
  return clone;
}

window.KANBAN = window.KANBAN || {};

function placeAppend(container, node, _seconds) {
  container.appendChild(node);
}

window.KANBAN.applyPatch = function applyPatch(returned, ctx = {}) {
  const {
    newEmployeeId,           // куда рисуем (строковый id сотрудника, '0' = backlog)
    templateEl,              // эталон для клонирования (если нет — возьмём ниже)
    movedEl,                 // запасной эталон
    movedBitrixId,           // bitrix_id для нового DOM
    placeNode = placeAppend  // стратегия вставки (append / insert by time)
  } = ctx;

  const tpl = templateEl || movedEl
           || document.querySelector('.kanban-item')
           || document.querySelector('.card.shadow.mb-3');

  // 1) DELETE
  if (Array.isArray(returned.delete) && returned.delete.length) {
    const uniq = [...new Set(returned.delete.map(String))];
    for (const delId of uniq) {
      document.getElementById(delId)?.remove();
    }
  }

  // 2) CHANGE
  if (returned.change && Object.keys(returned.change).length) {
    for (const k of Object.keys(returned.change)) {
      const patch = returned.change[k]; // { time, date }
      const node = document.getElementById(String(k));
      if (!node) continue;
      updateCardTimeSeconds(node, Number(patch.time) || 0);
      setScissorsHandler(node, Number(patch.time) || 0, k);

      const needDate = patch.date;
      if (needDate) {
        const targetCat = findCategoryBy(newEmployeeId, needDate);
        if (targetCat && node.parentElement !== targetCat) {
          targetCat.appendChild(node);
        }
      }
    }
  }

  // 3) ADD
  if (returned.add && Object.keys(returned.add).length) {
    const bx = movedBitrixId || tpl?.getAttribute('data-bs-bitrix') || '';
    for (const k of Object.keys(returned.add)) {
      const add = returned.add[k]; // { time, title, date }
      const targetCat = findCategoryBy(newEmployeeId, add.date);
      if (!targetCat || !tpl) continue;

      let node = document.getElementById(String(k));
      if (!node) {
        node = ensureCardExistsLike(tpl, k, { title: add.title, bitrix: bx, time: add.time });
        setScissorsHandler(node, Number(add.time) || 0, k);
        placeNode(targetCat, node, Number(add.time) || 0);
      } else {
        updateCardTimeSeconds(node, Number(add.time) || 0);
        setScissorsHandler(node, Number(add.time) || 0, k);
        const t = node.querySelector('.card-header-title'); if (t) t.textContent = add.title;
        if (node.parentElement !== targetCat) targetCat.appendChild(node);
      }
    }
  }

  // 4) спец-кейс backlog (если используешь expect)
  if (String(newEmployeeId) === '0' && typeof returned.expect !== 'undefined') {
    const backlogCat = findCategoryBy('0', '0000-00-00');
    if (backlogCat) {
      const node = document.getElementById(String(returned.expect));
      if (node && node.parentElement !== backlogCat) backlogCat.appendChild(node);
    }
  }
};

// (опционально) сделаем хелперы доступными снаружи
window.KANBAN.findCategoryBy = findCategoryBy;
window.KANBAN.ensureCardExistsLike = ensureCardExistsLike;
window.KANBAN.updateCardTimeSeconds = updateCardTimeSeconds;
window.KANBAN.setScissorsHandler = setScissorsHandler;


if (categories) {
  const sortable = new Sortable(categories, {
    draggable: '.kanban-item',
    mirror: {
      constrainDimensions: true,
    },
  });

  const dragMemo = new WeakMap();

  sortable.on('sortable:start', (e) => {
    const src = e.data.dragEvent?.data?.source || e.data.dragEvent?.source || e.data.dragEvent?.data?.originalSource;
    if (!src) return;
    dragMemo.set(src, {
      oldContainer: e.data.oldContainer,
      oldIndex: e.data.oldIndex,
      prevSibling: src.previousElementSibling
    });
  });

  sortable.on('sortable:stop', (e) => {
    const movedEl = e.data.dragEvent?.data?.source || e.data.dragEvent?.source || e.data.dragEvent?.data?.originalSource;
    const newContainer = e.data.newContainer;                          // новый контейнер (ul)
    const oldContainer = e.data.oldContainer;                          // старый контейнер (ul)
    const memo = dragMemo.get(movedEl) || {};
    const oldIdx = Number.isInteger(memo.oldIndex) ? memo.oldIndex : e.data.oldIndex;

    const newCat = closestCategory(newContainer);
    const oldCat = closestCategory(oldContainer);
    if (!newCat || !oldCat) return;

    const newEmployeeId = newCat.id;                                   // строка!
    const oldEmployeeId = oldCat.id;
    const newDate = newCat.getAttribute('data-associated-date');
    const oldDate = oldCat.getAttribute('data-associated-date');

    const movedLocalId = parseInt(movedEl.id, 10);
    const movedBitrixId =
    movedEl.getAttribute('data-bs-bitrix') ||
    movedEl.querySelector('[data-bs-bitrix]')?.getAttribute('data-bs-bitrix') || '';

    // 0) Жёсткий запрет перетаскивания, если есть списанное время
    const timeSpentSec = getTimeSpentSeconds(movedEl);        // у самой карточки
    if (timeSpentSec > 0) {
      rollback(movedEl, memo.oldContainer || oldContainer, oldIdx, memo.prevSibling);
      showToast({ status: 'error', message: 'Нельзя перемещать задачу: уже есть списанное время' });
      return;
    }

    // 0.1) Запрет, если у ЛЮБОЙ подтаски из этой группы уже есть списанное время
    if (hasTrackedSibling(movedBitrixId, movedLocalId)) {
      rollback(movedEl, memo.oldContainer || oldContainer, oldIdx, memo.prevSibling);
      showToast({ status: 'error', message: 'Нельзя перемещать: у подтаски этой задачи уже есть списанное время' });
      return;
    }

    const plannedSec = getPlannedSeconds(movedEl);
    console.log(plannedSec, newEmployeeId)
    if (String(newEmployeeId) !== '0' && plannedSec === 0) {
      // откат визуально
      rollback(movedEl, memo.oldContainer || oldContainer, oldIdx, memo.prevSibling);

      // подготовим модалку #emptyTaskTime
      const modalEl = document.getElementById('emptyTaskTime');
      console.log(modalEl);
      if (modalEl) {
        modalEl.dataset.taskId = String(movedLocalId);
        modalEl.dataset.targetEmployee = String(newEmployeeId);
        modalEl.dataset.targetDate = String(newDate || '');

        // совместимость со старой логикой
        const hidden = document.getElementById('inputHidden');
        if (hidden) hidden.value = String(movedLocalId);

        const h = document.getElementById('emptyTaskTimeHours');
        const m = document.getElementById('emptyTaskTimeMinutes');
        if (h) h.value = '';
        if (m) m.value = '';

        // показать модалку
        console.log('Создаем модалку')
        let el = document.createElement('a');

        el.setAttribute('data-bs-toggle','modal')
        el.setAttribute('data-bs-target','#emptyTaskTime')
        el.setAttribute('aria-haspopup','true')
        el.setAttribute('aria-expanded','false')
        console.log(el)

        document.body.appendChild(el)
        el.click()
        document.body.removeChild(el)
      }

      return; // ничего не отправляем — ждём подтверждение в модалке
    }

    // Собираем payload для /api/delegate/
    const postData = new URLSearchParams({
      task_id: String(movedLocalId),
      user_id: String(newEmployeeId), // "0" — backlog
      date: String(newDate || '')
    });

    fetch('/api/delegate/', {
      method: 'POST',
      headers: { 'X-CSRFToken': getCookie('csrftoken') },
      body: postData
    })
    .then(r => r.json().then(j => [r, j]))
    .then(([r, data]) => {
      console.log(r, data)
      if (!r.ok || data.status !== 'success') {
        const msg = (data && (data.message || data.error)) || 'Ошибка при делегировании';
        // откатим визуально
        rollback(movedEl, memo.oldContainer || oldContainer, oldIdx, memo.prevSibling);
        console.error('delegate error:', data);
        showToast({ status: 'error', message: msg });
        return;
      }

      const returned = data.data || {};
      try {
          window.KANBAN.applyPatch(returned, {
            newEmployeeId,
            movedEl,
            movedBitrixId
        });
      } catch (err) {
        console.error('applyPatch error:', err);
        showToast({ status: 'error', message: 'Ошибка отрисовки ответа' });
        return;
      }

      // визуальный успех
      showToast({ status: 'success', message: 'Исполнитель обновлён' });
    })
    .catch(err => {
      console.error(err);
      // откат визуально
      rollback(movedEl, memo.oldContainer || oldContainer, oldIdx, memo.prevSibling);
      showToast({ status: 'error', message: 'Сеть недоступна или сервер не ответил' });
    });
  });
  window.sortable = sortable
}

function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== '') {
    const cookies = document.cookie.split(';');
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      if (cookie.substring(0, name.length + 1) === (name + '=')) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}
function rollback(el, oc, oi, prev) {
  // вернём строго на прежнее место
  if (prev && prev.parentNode === oc) {
    oc.insertBefore(el, prev.nextSibling);
  } else {
    const ref = oc.children[oi] || null;
    oc.insertBefore(el, ref);
  }
}

function getPlannedSeconds(node) {
  const blocks = node.querySelectorAll('.col-auto.text-secondary');
  const display = blocks[1] || blocks[0];
  const val = display ? display.getAttribute('data-time') : null;
  const num = Number(val);
  return Number.isFinite(num) ? num : 0;
}

function floatToTime(value) {
    try {
        // Преобразуем в число и переводим часы в минуты
        const totalMinutes = Math.round(parseFloat(value) / 60);
        const hours = Math.floor(totalMinutes / 60);
        const minutes = totalMinutes % 60;

        const parts = [];
        
        // Форматируем часы с правильным склонением
        if (hours > 0) {
            let hourWord;
            if (hours === 1) {
                hourWord = 'час';
            } else if (hours >= 2 && hours <= 4) {
                hourWord = 'часа';
            } else {
                hourWord = 'часов';
            }
            parts.push(`${hours} ${hourWord}`);
        }

        // Форматируем минуты с правильным склонением
        if (minutes > 0 || parts.length === 0) {
            let minuteWord;
            if (minutes === 1) {
                minuteWord = 'минута';
            } else if (minutes >= 2 && minutes <= 4) {
                minuteWord = 'минуты';
            } else {
                minuteWord = 'минут';
            }
            parts.push(`${minutes} ${minuteWord}`);
        }

        return parts.join(' ') || '0 минут';
    } catch (e) {
        // В случае ошибки возвращаем исходное значение как строку
        return String(value);
    }
}

links.forEach((link) => {
  link.addEventListener('click', () => {
    toggleItems(link);
  });
});

forms.forEach((form) => {
  form.addEventListener('reset', function () {
    toggleItems(form);
  });

  form.addEventListener('submit', function (e) {
    e.preventDefault();
  });
});
// Make available globally
window.Sortable = Sortable;
